.Search-Box-Container {
  padding-top: 20px;
  padding-bottom: 20px;
  background: linear-gradient(
    to left,
    #fffc54 0%,
    #fffc54 50%,
    #ffffff 50%,
    #ffffff 100%
  );
}

input {
  background: transparent;
  margin-top: 15px;
  margin-left: 20px;
  width: 65%;
  border-radius: 20px;
  height: 30px;
  border: 1.5px solid rgba(0, 0, 0, 0.1);
}
input[placeholder] {
  padding-left: 20px;
}

button {
  border-style: none;
  background-color: #fffc54;
  font-size: 45px;
  cursor: pointer;
  margin: 10px 20px 5px 10px;
}
.title-container {
  text-align: right;
}
.title {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-style: italic;
  font-size: 20px;
  color: rgba(7, 7, 7, 0.911);
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 0;
  margin-top: 13px;
  line-height: 20px;
}

.second-container {
  padding-left: 100px;
  display: flex;
  flex-direction: row;
  position: relative;
}

.io5-icon {
  position: absolute;
  height: 17px;
  width: 18px;
  top: 39%;
  bottom: 89.8%;
  left: 68.9%;
  right: 88%;
}

.icon {
  font-size: 20px;
  margin: 10px 20px 5px 10px;
}
@media screen and (max-width: 600px) {
  .icon,.io5-icon {
    display: none;
  }
 
  .Search-Box-Container {
    padding-top: 0;
    padding-bottom: 0;
    background: linear-gradient(
      to left,
      #fffc54 0%,
      #fffc54 50%,
      #ffffff 50%,
      #ffffff 100%
    );
  }
  .second-container {
    display: flex;
    flex-direction: column;
    padding: 25px;
    margin: auto;
    
  }
  .title{
    text-align: center;
    margin-top: 0;
  }
  input{
    font-size: 12px;
    width: 75%;
    border: 1.5px solid rgba(0, 0, 0, 0.5);
  }
 
 
}
