* {
  box-sizing: border-box;
}

.monster-set2 {
  display: flex;
  flex-direction: column;
  float: left;
  width: 50%;
  background-color: #ffffff;
  position: fixed;
  z-index: -1;
}
.set2,
.set3 {
  margin-top: 8px;
  height: 100vh;
  width: 85%;
  overflow: scroll;
}

.monster-set3 {
  display: flex;
  flex-direction: column;
  float: right;
  width: 50%;
  background-color: #fffc54;
  z-index: 2;
}

h1 {
  display: flex;
  justify-content: center;
  font-style: italic;
  margin-bottom: 36px;
  font-size: 50px;
}
.btn {
  display: inherit;
  align-items: center;
  padding-top: 20px;
  margin-left: 45px;
  margin-bottom: 50px;
}
.monster-card-btn {
  background-color: black;
  color: white;
  align-items: center;
  font-size: 20px;
  font-family: "Times New Roman", Times, serif;
  border-radius: 2%;
  width: 30%;
  text-align: center;
}

@media screen and (max-width: 600px){
    h1{
        font-size: 30px;
    }
  .btn{
    display: flex;
    flex-direction: column;
    margin: auto;
    padding-top: 10px;
    padding-bottom: 10px;
  }
    .monster-card-btn{
      display: flex;
      font-size: 10px;
      width: 100%;
      height: 45%;
      font-size: 20px;


    }
}