.card-container-set2,
.monster-name {
  display: flex;
  justify-content: center;
}

.img-set2 {
  background-color: #e4e0e0;
  width: 50%;
  height: 50%;
  /* border-radius: 50%; */
}

@media screen and (max-width: 600px) {
  .img-set2 {
    width: 65%;
    border-radius: 10%;
  }
  .monster-name {
    align-items: center;
    display: inline-block;
    font-size: 15px;
  }
}
