* {
  box-sizing: border-box;
}
.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 25px;
  border-bottom: 1.5px solid rgba(0, 0, 0, 0.1);
  background: linear-gradient(
    to left,
    #fffc54 0%,
    #fffc54 50%,
    #ffffff 50%,
    #ffffff 100%
  );
}

.text-changes-container,
.textThatChanges {
  height: 15px;
  margin: 0;
}
.textThatChanges {
  font-size: 12px;
}
.flag-container {
  float: left;
}
.flag {
  font-size: 12px;
  font-family: "Times New Roman", Times, serif;
  color: rgb(87, 87, 87);
  margin: 0;
}
.support-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.support {
  position: relative;
  font-size: 12px;
  font-family: "Times New Roman", Times, serif;
  margin-left: 20px;
  margin-top: 3px;
  margin-bottom: 0;
  float: right;
  z-index: 1;
}
.connect {
  padding-right: 20px;
}

@media screen and (max-width: 600px){
    .support-container{
        display: none;
    }
    .flag{
        display: none;
    }
    .text-changes-container{
      margin: auto;
    }
}