.ul-div {
  background: linear-gradient(
    to left,
    #fffc54 0%,
    #fffc54 50%,
    #ffffff 50%,
    #ffffff 100%
  );
}

ul {
  width: 65vw;
  padding: 0;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
li {
  padding: 0;
  list-style: none;
}
li:hover {
  border-bottom: 2px solid black;
  cursor: pointer;
}

@media screen and (max-width: 600px){
  ul{
    display: none;
  }
}
